import React, { useState, useEffect } from 'react';
import Header from '../header';
import './clock.scss';

function Clock(props) {
    const [secondAngle, setSecondAngle] = useState(0);
    const [minuteAngle, setMinuteAngle] = useState(0);
    const [hourAngle, setHourAngle] = useState(0);
    let secondAdditionAngle = 360/60;
    let minuteAdditionAngle = 6;
    let hourAdditionAngle = 30;
    let hourAdditionAngle2 = 0;
    setTimeout(() => {
        hourAdditionAngle2 = 0;
        if(minuteAngle<=180 && minuteAngle>90){
            hourAdditionAngle2 = 30*(1/4);
        }
        if(minuteAngle<=270 && minuteAngle>180){
            hourAdditionAngle2 = 30*(2/4);
        }
        if(minuteAngle<=360 && minuteAngle>270){
            hourAdditionAngle2 = 30*(3/4);
        }
        let today = new Date();
        setSecondAngle(today.getSeconds() * secondAdditionAngle);
        setMinuteAngle(today.getMinutes() * minuteAdditionAngle);
        setHourAngle((today.getHours() * hourAdditionAngle) + hourAdditionAngle2);
    }, 1000);    
    return (
        <>
            <Header number={props.number} />
            <div className="App-container" style={{'backgroundColor':'#628395', 'minHeight':'calc(100vh - 30px)', 'display':'flex', 'alignItems': 'center', 'justifyContent':'center'}}>
                <div className="clock-container">
                    <div className="clock-minute-mark-container">
                        {
                            [...Array(60).keys()].map((value, key)=>(
                                <div key={key} className="clock-minute-marker" style={{transform:`rotate(${((value+1)/60*360)}deg)`}}>{value+1}</div>
                            ))
                        }
                    </div>
                    <div className="clock-hour-hand" style={{transform : `rotate(${hourAngle}deg)`}}></div>
                    <div className="clock-minute-hand" style={{transform : `rotate(${minuteAngle}deg)`}}></div>
                    <div className="clock-second-hand" style={{transform : `rotate(${secondAngle}deg)`}}></div>
                    <div className="clock-hand-btn"></div>
                </div>
            </div>
        </>
    );
}

export default Clock;
