import React, { useState, useEffect } from 'react';
import Header from '../header';
import './clock.scss';

function DigitalClock(props) {
    const [secondAngle, setSecondAngle] = useState(0);
    const [minuteAngle, setMinuteAngle] = useState(0);
    const [hourAngle, setHourAngle] = useState(0);
    const [timeType, setTimeType] = useState('12');
    let figureHeight = 96;
    setTimeout(() => {
        let today = new Date();
        setSecondAngle(today.getSeconds() * figureHeight);
        setMinuteAngle(today.getMinutes() * figureHeight);
        if(timeType=='12' && (today.getHours>12)){
            setHourAngle((today.getHours()-12) * figureHeight);
        }else{
            setHourAngle(today.getHours() * figureHeight);
        }
    }, 1000);   
    function toggleTimeType(){
        if(timeType=='12'){
            setTimeType('24');
        }else{
            setTimeType('12');
        }
    }
    const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
    const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    let today = new Date();
    return (
        <>
            <Header number={props.number} />
            <div className="App-container" style={{'backgroundColor':'#628395', 'minHeight':'calc(100vh - 30px)', 'display':'flex', 'alignItems': 'center', 'justifyContent':'center'}}>
                <div className="digital-clock-container">
                    <div className="clock-hour-digit" style={{transform : `translateY(-${hourAngle}px)`}}>
                        {
                            [...Array(24).keys()].map((value, key)=>(
                                <div key={key} className="digital-figure-container">{(value<10)?'0':''}{value}</div>
                            ))
                        }
                    </div>
                    <div className="clock-divider">:</div>
                    <div className="clock-minute-digit" style={{transform : `translateY(-${minuteAngle}px)`}}>
                        {
                            [...Array(60).keys()].map((value, key)=>(
                                <div key={key} className="digital-figure-container">{(value<10)?'0':''}{value}</div>
                            ))
                        }
                    </div>
                    <div className="clock-divider">:</div>
                    <div className="clock-second-digit" style={{transform : `translateY(-${secondAngle}px)`}}>
                        {
                            [...Array(60).keys()].map((value, key)=>(
                                <div key={key} className="digital-figure-container">{(value<10)?'0':''}{value}</div>
                            ))
                        }
                    </div>
                </div>
                <div className="date-container">
                    {days[today.getDay()] + ', ' + months[today.getMonth()] + ' ' + today.getDate() + ' ' + today.getFullYear()}
                </div>
                <div className="time-type">
                    <input type="checkbox" checked={(timeType=='24')?true:false} onChange={toggleTimeType}/>
                    24-hour clock
                </div>
            </div>
        </>
    );
}

export default DigitalClock;
