import React, { useState } from 'react';
import { Link } from "react-router-dom";
function MenuList(props) {
    let data = props.menu;
    const [menuCollapse, setMenuCollapse] = useState(false);
    const menuToggle = () => {
      setMenuCollapse(!menuCollapse);
    }
    return (
      <div className="App-menu-container">
        <div className={`menu-toggle ${(menuCollapse)?'menu-open':''}`} onClick={()=>menuToggle()}>
          <div className=""></div>
          <div className=""></div>
          <div className=""></div>
        </div>
        <nav className={`menu-container ${(menuCollapse)?'show':''}`}>
          <ul className="menu-nav-list">
            {
              data.map((value, key)=>(
                <li key={key} className="menu-item">
                  <Link className="menu-item-link" to={`/${value.project_url}`} onClick={()=>menuToggle()}>{value.project_name}</Link>
                </li>
              ))
            }
          </ul>
        </nav>
      </div>
    );
}

export default MenuList;
